import { getLocaleData } from 'grommet/utils/Locale';
import { addLocaleData } from 'react-intl';
import flatten from 'flat';
import localeData from 'react-intl/locale-data/he';
import translations from './he.json';

addLocaleData(localeData);
const localData = getLocaleData(flatten(translations), 'he');
export { localData };



